import React, { Component, useEffect } from 'react';
import { connect } from 'react-redux';
import moment from "moment";
import "moment/locale/tr";
import MainNavigation from "@navigation/MainNavigation";
import "@scss/index.scss";
import { updateWindowSize, updateWindowScrolls } from './modules/static/window'
import "../node_modules/animate.css/animate.min.css";
import "./App.scss";

type AppProps = {
  updateWindowSize: (payload: any) => void,
  updateWindowScrolls: (payload: any) => void,
}

const App: React.FunctionComponent<AppProps> = (props) => {
  useEffect(() => {
    window.onresize = () => {
      props.updateWindowSize({ width: window.innerWidth, height: window.innerHeight });
    }

    window.onscroll = () => {
      props.updateWindowScrolls({ scrollX: window.scrollX, scrollY: window.scrollY });
    }
    moment.locale('tr')
    console.log = () => { }
    console.error = () => { }
    console.warn = () => { }
    console.table = () => { }
    return () => {
      window.onresize = null;
      window.onscroll = null;
    }
  }, [])
  return (<MainNavigation />);
}

export default connect((state) => ({

}), {
  updateWindowSize,
  updateWindowScrolls
})(App);
